.request {
  position: relative;
  background: #fff;
  background-repeat: repeat;
  background-image: url('../images/bg/bg-request-form.jpg');
  width: 100%;
  height: 100%;

  &__container {
    padding: 60px 50px;
    position: relative;
    z-index: 1;
  }

  &__title {
    margin: 0 0 20px;
    font-family: $font-base;
    font-weight: 500;
    font-size: 40px;
    color: $color-link;
    line-height: 1.2;
  }

  &__text {
    font-family: $font-base;
    font-weight: normal;
    font-size: 20px;
    color: $color-description;
    line-height: 1.5;

    &_mt {
      margin-top: 25px;
    }

  }

}

.request-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  overflow-y: auto;
  transform: translateX(100%);
  transition: transform .15s cubic-bezier(.4, 0, .2, 1);
  width: 500px;
  background: #fff;

  &.toggled {
    transform: translateX(0);
    transition-duration: 235ms;
  }

}

.request-mask {
  display: block;
  background-color: rgba(0, 0, 0, .5);
  bottom: 0;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateZ(0);
  transition: opacity .2s linear;
  visibility: hidden;
  z-index: 0;

  &.toggled {
    opacity: 1;
    visibility: visible;
    z-index: 998;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .request {

    &__container {
      padding: 50px 40px;
    }

    &__title {
      font-size: 30px;
    }

    &__text {
      font-size: 18px;
      line-height: 1.5;

      &_mt {
        margin-top: 25px;
      }

    }

  }


}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .request {

    &__container {
      padding: 30px 15px;
    }

    &__title {
      font-size: 28px;
    }

    &__text {
      font-size: 16px;
      line-height: 1.5;
      margin-top: 25px;

      &_mt {
        margin-top: 25px;
      }

    }

  }

  .request-modal {
    width: 280px;
  }


}

