.menu {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  align-items: center;
  margin: 0;
  padding: 0;

  $g: &;

  &__item {
    position: relative;

    &:hover {

      #{$g}__link {
        color: $color-link;
      }

      & .menu-sub {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
        transition-duration: 235ms;
      }

    }

    &:not(:first-child) {
      margin-left: 25px;
    }

  }

  &__link {
    display: inline-block;
    padding: 10px 0;
    font-family: $font-base;
    font-weight: 400;
    font-size: 20px;
    color: $color-description;
    text-decoration: none;
    transition: color ease-in-out .2s;

    &_active {
      color: $color-link;
    }

  }

}

.menu-sub {
  list-style: none;
  z-index: 999;
  position: absolute;
  top: 100%;
  width: 300px;
  padding: 10px 0;
  background: #fff;
  max-width: none;
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
  border-radius: 6px;
  display: block;
  visibility: hidden;
  pointer-events: auto;
  opacity: 0;

  transform: translateY(-10px);
  transition: all .15s cubic-bezier(.4, 0, .2, 1);

  transition-delay: .2s;

  &__item {

  }

  &__link {
    display: block;
    background: #fff;
    color: $color-description;
    font-family: $font-base;
    font-weight: normal;
    font-size: 18px;
    padding: 10px 20px;
    line-height: 20px;
    text-decoration: none;
    text-transform: none;
    vertical-align: baseline;
    transition: color ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

    &_active {
      color: $color-link;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .menu {
    flex-direction: column;
    align-items: normal;
    padding-bottom: 50px;

    &__item {
      border-bottom: 1px solid #e3e3e3;

      &:not(:first-child) {
        margin-left: 0;
      }

      &:last-child {
        margin: 25px;
        border-bottom: none;
      }

      &:nth-child(1) {
        order: 2;
      }
      &:nth-child(2) {
        order: 3;
      }
      &:nth-child(3) {
        order: 4;
      }
      &:nth-child(4) {
        order: 5;
      }
      &:nth-child(5) {
        order: 6;
      }
      &:nth-child(6) {
        order: 7;
      }
      &:nth-child(7) {
        order: 1;
      }

    }

    &__link {
      display: block;
      padding: 15px 25px;
      font-size: 22px;
    }

  }

  .menu-sub {
    position: relative;
    width: 100%;
    padding: 0 0 15px;
    box-shadow: none;
    border-radius: 0;
    visibility: visible;
    opacity: 1;

    transform: translateY(0);

    &__item {

    }

    &__link {
      font-weight: 300;
      font-size: 18px;
      padding: 10px 25px;
      line-height: 20px;
    }

  }

}

