.header {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #e3e3e3;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
  z-index: 99;
  transition: all 0.5s;

  &_scrollUp {
    transform: translateY(-159px);
  }

  &__top-wrap {
    background: #008435;
  }

  &__top-container {
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
  }

  &__bottom-wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
  }

  &__logo {

  }

  &__nav-btn {
    display: none;
  }

  &__nav {

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .header {

    &__top-container {
      padding-top: 15px;
      padding-bottom: 15px;
      justify-content: center;
    }

    &__bottom-wrap {
      padding: 10px 0;
      justify-content: center;
    }

    &__logo {

    }

    &__nav-btn {
      display: inline-block;
      position: absolute;
      top: 23px;
      left: 0;
      min-width: 30px;
      height: 30px;
      color: $color-description;
      background: transparent;
      border: none;

      &:focus {
        outline: 3px dotted #EB3F57;
      }

    }

    &__nav {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      z-index: 99;
      overflow-y: auto;
      transform: translateX(-100%);
      transition: transform .15s cubic-bezier(.4, 0, .2, 1);
      width: 280px;
      background: #fff;

      &.toggled {
        transform: translateX(0);
        transition-duration: 235ms;
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .header {

    &__nav-btn {

      &:focus {
        outline: 0;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .header {

    &__top-container {
      display: none;
    }

    &__nav-btn {
      top: 17px;
    }

  }

}

