* {
  box-sizing: border-box;
}

html {
  letter-spacing: 0.02em;
  font-family: $font-base;
}

html, body {
  position: relative;
  height: 100%;
}

body {

  &.toggled {
    overflow: hidden;
  }

}

a:focus {
  outline: 3px dotted $color-link;
}

