.content-wrap {
  flex: 1 1 auto;
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
}

.content {

  &_pl {
    padding-left: 40px;
  }

  &_pr {
    padding-right: 40px;
  }

  & > *:first-child {
    margin-top: 0;
  }

  & hr {
    margin: 20px 0;
    height: 1px;
    background: #dee2e6;
    border: 0;
  }

  & h1, & h2, & h3, & h4 {
    font-family: $font-base;
    font-weight: 500;
    line-height: 1.2;
    color: $color-title;
  }

  & h1 {
    font-size: 48px;
  }

  & h2 {
    font-size: 44px;
  }

  & h3 {
    font-size: 40px;
  }

  & h4 {
    font-size: 36px;
  }

  & p {
    font-family: $font-base;
    font-weight: normal;
    font-size: 20px;
    color: $color-description;
    line-height: 1.5;

    & > img {
      width: 100% !important;
      max-width: 100%;
      height: auto !important;
      border-radius: 5px;
    }

  }

  & a {
    font-family: $font-base;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    word-wrap: break-word;
    overflow-wrap: break-word;
    //text-decoration: none;

    //&:hover {
    //  text-decoration: underline;
    //}

  }

  & > img {
    width: 100% !important;
    max-width: 100%;
    height: auto !important;
  }

  & ul, & ol {
    font-family: $font-base;
    font-weight: normal;
    font-size: 20px;
    color: $color-description;
    line-height: 1.5;
  }

  & table {
    //display: block;
    width: 100%;
    max-width: 100%;
    overflow-x: auto;
    border-collapse: collapse;
    margin: 30px 0;
    background-color: transparent;
    font-family: $font-base;
    font-weight: normal;
    font-size: 20px;
    color: $color-description;
  }

  & table thead th {
    padding: .75rem;
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
  }

  & table td, .table th {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
  }

  & th {
    text-align: inherit;
  }

}

.content-header-wrap {
  padding-top: 70px;
}

.content-title {
  margin: 0 0 20px;
  font-family: $font-base;
  font-weight: 500;
  font-size: 48px;
  color: $color-title;
  text-align: center;
  line-height: 1.2;
}

.content-date {
  display: inline-block;
  font-family: $font-base;
  font-weight: 300;
  font-size: 18px;
  color: $color-description;
  line-height: 1.5;
}

.content-category {
  display: inline-block;
  margin-left: 20px;
  font-family: $font-base;
  font-weight: 300;
  font-size: 18px;
  color: $color-link;
  line-height: 1.3;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

.content-tag {
  margin-top: 10px;
  font-family: $font-base;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.3;

  & > a {
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

}

.content-avatar {
  width: 100%;
  height: 500px;
  margin: 20px 0;
  object-fit: cover;
}

.content-img {
  position: relative;
  height: 100%;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    right: -20px;
    bottom: -20px;
    background: $color-link;
  }

  &__img {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .content {

    &_pl {
      padding-left: 0;
    }

    &_pr {
      padding-right: 0;
    }

    &_mt-md {
      margin-top: 50px;
    }

    & h1 {
      font-size: 38px;
    }

    & h2 {
      font-size: 34px;
    }

    & h3 {
      font-size: 30px;
    }

    & h4 {
      font-size: 26px;
    }

    & p {
      font-size: 18px;
    }

    & a {
      font-size: 18px;
    }

    & ul, & ol {
      font-size: 18px;
    }

    & table {
      font-size: 18px;
    }

  }

  .content-img {
    height: 100%;
    max-height: 500px;

    &:after {
      right: -10px;
      bottom: -10px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .content {

    &_mt-md {
      margin-top: 30px;
    }

    &_mt-sm {
      margin-top: 30px;
    }

    & h1 {
      font-size: 28px;
    }

    & h2 {
      font-size: 24px;
    }

    & h3 {
      font-size: 20px;
    }

    & h4 {
      font-size: 16px;
    }

    & p {
      font-size: 16px;
    }

    & a {
      font-size: 16px;
    }

    & ul, & ol {
      font-size: 16px;
    }

    & table {
      display: block;
      font-size: 16px;
    }

  }

  .content-img {
    max-height: 300px;

    &:after {
      right: -5px;
      bottom: -5px;
    }

  }

  .content-header-wrap {
    padding-top: 30px;
  }

  .content-title {
    font-size: 30px;
    margin-bottom: 5px;
    text-align: left;
  }

  .content-avatar {
    height: 300px;
  }

}

