.plus-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px;

  $g: &;

  &_1 {

    #{$g}__item {
      width: 100%;
    }

  }

  &__item {
    width: 50%;
    padding: 20px;
  }

}

.plus {
  display: flex;

  &__icon-wrap {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 110px;
    height: 110px;
    border-radius: 100%;
    background: transparent;
    border: 1px solid $color-link;
  }

  &__icon {
    width: 50px;
    height: 50px;

    & * {
      fill: $color-link;
    }

  }

  &__content {
    padding-left: 20px;
  }

  &__title {
    margin: 0!important;
    font-family: $font-base;
    font-weight: 500!important;
    font-size: 24px!important;
    color: $color-title;
    line-height: 1.2!important;
  }

  &__descr {
    margin: 10px 0 0!important;
    font-family: $font-base;
    font-weight: 300!important;
    font-size: 18px!important;
    color: $color-description-light;
    line-height: 1.3!important;
  }

}

