/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .plus-list {
    margin: 0 -15px;

    $g: &;

    &_1 {

      #{$g}__item {
        width: 100%;
      }

    }

    &__item {
      width: 100%;
      padding: 15px;
    }

  }

  .plus {

    &__icon-wrap {
      min-width: 80px;
      height: 80px;
    }

    &__icon {
      width: 40px;
      height: 40px;
    }

    &__content {
      padding-left: 15px;
    }

    &__title {
      font-size: 22px!important;
    }

    &__descr {
      font-size: 16px!important;
    }

  }

}

