.section {
  padding: 70px 0 60px;

  &_blue {
    background: #f8fafe;
  }

  &__title {
    position: relative;
    margin: 0 0 60px;
    font-family: $font-base;
    font-weight: 500;
    font-size: 48px;
    color: $color-title;
    text-align: center;
  }

  &__btn-wrap {
    margin-top: 60px;
    text-align: center;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .section {
    padding: 60px 0 50px;

    &__title {
      margin: 0 0 50px;
      font-size: 38px;
    }

    &__btn-wrap {
      margin-top: 50px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .section {
    padding: 40px 0 30px;

    &__title {
      margin: 0 0 20px;
      font-size: 28px;
    }

    &__btn-wrap {
      margin-top: 30px;
    }

  }

}

