
.accordion-menu {

  &__title {
    margin: 0;
    font-family: $font-base;
    font-weight: normal;
    font-size: 24px;
    padding: 10px 0;
    color: $color-link;
  }

  & ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  & li.open > ul {
    display: block;
  }

  & li > ul {
    /* position: absolute; */
    display: none;
    height: 0;
    overflow: hidden;
    transition: height .2s ease-in-out;
    margin-left: 25px;
  }

  & ul.is-visible {
    display: block;
    /* height: auto; */
  }

  & li {
    border-bottom: 1px solid #e3e3e3;
    position: relative;
    overflow: hidden;
    transition: all .4s ease;
  }

  & li:last-child {
    border: none;
  }

  & li::after {
    content: "";
    display: block;
    clear: both;
  }

  & li > a > svg {
    display: block;
    position: relative;
    fill: rgb(48, 48, 48);
    min-width: 10px;
    float: right;
    transition: all .3s ease;
  }

  & li.open > a > svg {
    transform: rotate(90deg);
  }

  & a {
    font-family: $font-base;
    font-weight: normal;
    font-size: 20px;
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    color: $color-description;
    transition: color ease-in-out .2s;
    outline: none;

    &:hover {
      color: $color-link;
    }

  }


}


/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .accordion-menu {

    &__title {
      font-size: 22px;
    }

    & a {
      font-size: 16px;
      padding: 10px 0;
    }

  }

}

