.tag-list {

}

.tag {
  display: inline-block;
  padding: 5px 10px;
  font-family: $font-base;
  font-weight: 400;
  color: #000;
  font-size: 16px;
  background: #fff;
  text-transform: uppercase;
}

.tag-nav {
}

.tag-menu {
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
  padding: 0;
  list-style: none;

  &__item {
    padding: 10px;
  }

  &__link {
    display: inline-block;
    padding: 5px 25px;
    border: 1px solid #cfcfcf;
    border-radius: 1px;
    font-family: $font-base;
    font-weight: 400;
    font-size: 24px;
    color: $color-description;
    text-decoration: none;
    transition: all ease-in-out .2s;

    &:first-letter {
      color: $color-link;
    }

    &:hover {
      color: $color-link;
      border-color: $color-link;

      &:first-letter {
        color: $color-link;
      }

    }

    &_active {
      background: rgba(207, 207, 207, 0.3);

      &:first-letter {
        color: $color-description;
      }

    }

    & > span {
      display: inline-block;
      padding: 0 6px 4px;
      color: $color-link;
      border: 1px solid $color-link;
      border-radius: 15px;
      line-height: 0.8;
    }

  }

}

