/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .tag {
    padding: 4px 10px 2px;
    font-size: 12px;
  }

  .tag-menu {
    margin: -2px;

    &__item {
      padding: 2px;
    }

    &__link {
      padding: 5px 20px;
      font-size: 16px;

      & > span {
        margin-left: 3px;
        padding: 1px 5px 2px;
      }

    }

  }

}

