.youtube-wrap {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    right: -20px;
    bottom: -20px;
    background: $color-link;
  }

}

.youtube {
  background-color: #000;
  margin: 40px 0;
  position: relative;
  z-index: 1;
  padding-top: 56.25%;
  overflow: hidden;
  cursor: pointer;
}
.youtube img {
  width: 100%;
  top: -16.82%;
  left: 0;
  opacity: 0.7;
}
.youtube .play-button {
  position: relative;
  width: 80px;
  height: 80px;
  background-color: $color-link;
  //box-shadow: 0 0 30px rgba(37, 235, 0, 0.6);
  box-shadow: 0 0 0 0 rgba(0, 132, 53, 0.7);
  border-radius: 50%;
  z-index: 1;
  //opacity: 0.8;
  animation: pulse 3.25s infinite cubic-bezier(0.66, 0, 0, 1);
}
.youtube .play-button:before {
  content: "";
  margin-left: 4px;
  border-style: solid;
  border-width: 15px 0 15px 26.0px;
  border-color: transparent transparent transparent #fff;
}
.youtube img,
.youtube .play-button {
  cursor: pointer;
}
.youtube img,
.youtube iframe,
.youtube .play-button,
.youtube .play-button:before {
  position: absolute;
}
.youtube .play-button,
.youtube .play-button:before {
  top: 50%;
  left: 50%;
  transform: translate3d( -50%, -50%, 0 );
}
.youtube iframe {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

@-webkit-keyframes pulse {to {box-shadow: 0 0 0 45px rgba(232, 76, 61, 0);}}
@-moz-keyframes pulse {to {box-shadow: 0 0 0 45px rgba(232, 76, 61, 0);}}
@-ms-keyframes pulse {to {box-shadow: 0 0 0 45px rgba(232, 76, 61, 0);}}
@keyframes pulse {to {box-shadow: 0 0 0 45px rgba(232, 76, 61, 0);}}

