.gallery {
  padding: 50px;
  display: flex;
  flex-wrap: wrap;

  &_content {
    margin: 60px -2px;
    padding: 0;
  }

  &__item {
    margin: 0;
    padding: 2px;
    width: 25%;

    &:nth-child(1) {
      width: 25%;
    }
    &:nth-child(2) {
      width: 25%;
    }
    &:nth-child(3) {
      width: 50%;
    }
    &:nth-child(4) {
      width: 50%;
    }
    &:nth-child(5) {
      width: 25%;
    }
    &:nth-child(6) {
      width: 25%;
    }
    &:nth-child(7) {}
    &:nth-child(8) {}
    &:nth-child(9) {}

  }

  &__img {
    vertical-align: top;
    width: 100%;
    height: 400px;
    object-fit: cover;
  }

}

@media only screen and (max-width: 1200px) {

  .gallery {
    padding: 20px;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .gallery {
    padding: 0;

    &__img {
      height: 250px;
    }

  }


}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .gallery {
    margin: 30px 0;

    &_content {
      margin: 30px -2px;
    }

    &__item {
      width: 50%;

      &:nth-child(1) {
        width: 50%;
      }
      &:nth-child(2) {
        width: 50%;
      }
      &:nth-child(3) {
        width: 100%;
      }
      &:nth-child(4) {
        width: 50%;
      }
      &:nth-child(5) {
        width: 50%;
      }
      &:nth-child(6) {
        width: 100%;
      }
      &:nth-child(7) {}
      &:nth-child(8) {}
      &:nth-child(9) {}

    }

    &__img {
      height: 250px;
    }

  }


}

