.crumb {
  padding: 20px 0;
  font-family: $font-base;
  font-size: 18px;
  font-weight: 300;
  overflow: auto;
  text-align: center;

  $g: &;

  &__list {
    display: inline-flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;

    &_wt {

      & > li {
        &:after {
          color: #fff !important;
        }

        & > a {
          color: #fff !important;

          &:hover {
            color: #fff !important;
            opacity: .9 !important;
          }

        }

      }

      #{$g}__current {
        color: #fff !important;
      }

    }

    & > li {
      position: relative;
      padding: 0 20px 0 0;

      &:after {
        content: "/";
        font-size: 16px;
        color: $color-title;
        vertical-align: middle;
        position: absolute;
        top: 2px;
        right: 7px;
      }

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        padding: 0;
        &:after {
          content: "";
        }
      }

      & > span {
        white-space: nowrap;
      }

      & > a {
        color: $color-title;
        text-decoration: none;
        white-space: nowrap;
        transition: all .2s;

        &:hover {
          color: $color-link;
        }

      }

    }

  }

  &__current {
    color: $color-title;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .crumb {
    padding: 20px 0;

    &__list {
      flex-wrap: nowrap;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .crumb {
    padding: 10px 0;
    font-size: 14px;
    overflow: auto;

    $g: &;

    &__list {

      &_wt {
        justify-content: center;

        & > li {
          &:after {
            color: #fff !important;
          }

          & > a {
            color: #fff !important;

            &:hover {
              color: #fff !important;
              opacity: .9 !important;
            }

          }

        }

        #{$g}__current {
          color: #fff !important;
        }

      }

    }

    &__list {
      display: flex;
      width: 100%;

      & > li {
        padding: 0 15px 0 0;

        &:first-child {
          padding-left: 0;
        }

        &:after {
          content: "/";
          font-size: 12px;
          vertical-align: middle;
          position: absolute;
          top: 2px;
          right: 7px;
        }

        &:last-child {
          &:after {
            content: "";
          }
        }

        & > span {
          white-space: nowrap;
        }

        & > a {
          text-decoration: none;
          transition: all .2s;
          white-space: nowrap;
        }

      }

    }

  }
}

