.banner {

  &__img {
    position: relative;
    background-repeat: no-repeat;
    background-image: url('../images/bg/bg.jpg');
    background-size: cover;
    background-position: center center;
    width: 100%;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, .5);
    }

  }

  &__content {
    padding: 220px 0;
    position: relative;
    z-index: 1;
  }

  &__title {
    margin: 0;
    font-family: $font-base;
    font-weight: 500;
    font-size: 72px;
    color: #fff;
    line-height: 1.3;
    text-align: center;
  }

  &__dscr {
    margin: 40px 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 24px;
    color: #fff;
    line-height: 1.3;
    text-align: center;
  }


}

.banner-nav {
  display: flex;
  justify-content: center;
  margin: 40px 0 0;
  padding: 0;
  list-style: none;

  &__item:not(:first-child) {
    margin-left: 25px;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .banner {

    &__img {

    }

    &__content {
      padding: 150px 0;
    }

    &__title {
      font-size: 62px;
    }

    &__dscr {
      font-size: 20px;
    }


  }

  .banner-nav {
    display: flex;
    justify-content: center;
    margin: 40px 0 0;
    padding: 0;
    list-style: none;

    &__item:not(:first-child) {
      margin-left: 25px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .banner {

    &__img {

    }

    &__content {
      padding: 50px 0;
    }

    &__title {
      font-size: 30px;
    }

    &__dscr {
      margin: 20px 0 0;
      font-size: 16px;
    }


  }

  .banner-nav {
    margin: 20px 0 0;

    &__item:not(:first-child) {
      margin-left: 15px;
    }

  }


}

