.timer {
  display: flex;
  justify-content: center;
  width: auto;
  margin: 36px 0 0;
  padding: 0;
  list-style: none;
  font-family: $font-base;
  font-weight: 300;

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:nth-child(2) {
      &:before {
        position: absolute;
        content: "";
        top: 10px;
        left: 0;
        width: 1px;
        height: 115px;
        border-right: solid 1px rgba(255, 255, 255, .5);
      }

      &:after {
        position: absolute;
        content: "";
        top: 10px;
        right: 0;
        width: 1px;
        height: 115px;
        border-right: solid 1px rgba(255, 255, 255, .5);
      }
    }

    &:nth-child(3) {
      &:after {
        position: absolute;
        content: "";
        top: 10px;
        right: 0;
        width: 1px;
        height: 115px;
        border-right: solid 1px rgba(255, 255, 255, .5);
      }
    }

  }

  &__count {
    position: relative;
    display: block;
    padding: 0 19px;
    font-size: 72px;
    letter-spacing: 1.4px;
    color: #fff;
  }

  &__text {
    font-size: 24px;
    line-height: 1.4;
    letter-spacing: .3px;
    color: #fff;
  }

}
