.logo {
  display: inline-flex;
  text-decoration: none;
  width: 299px;
  height: 53px;

  &__img-wrap {
  }

  &__img {
    width: 102px;
    height: 48px;
  }

  &__text-wrap {
    padding-left: 20px;
  }

  &__text-lg {
    font-family: $font-base;
    font-weight: normal;
    font-size: 22px;
    color: #313131;
    text-transform: uppercase;
    line-height: 0.8;
  }

  &__text-sm {
    padding-top: 3px;
    font-family: $font-base;
    font-weight: 300;
    font-size: 16px;
    color: #757575;
    line-height: 1;
  }

}

