/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .logo {
    width: 218px;
    height: auto;

    &__img-wrap {
    }

    &__img {
      width: 83px;
      height: 39px;
    }

    &__text-wrap {
      padding-left: 10px;
    }

    &__text-lg {
      font-size: 16px;
    }

    &__text-sm {
      font-size: 12px;
    }

  }

}

