.author {
  margin-top: 108px;
  padding: 20px;
  border: 1px solid #e8e8e8;

  &__img-wrap {
    margin: -97px auto 0;
    width: 150px;
    height: 150px;
    border-radius: 100%;
    overflow: hidden;
  }

  &__img {
    width: 150px;
    height: 150px;
    object-fit: cover;
  }

  &__text {
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    font-style: italic;
    color: $color-title;
    line-height: 1.3;
  }

}
