.card-list {
  display: flex;
  flex-wrap: wrap;
  margin: -20px;

  $g: &;

  &_3 {

    #{$g}__item {
      width: 33.33%;
    }

  }

  &__item {
    width: 25%;
    padding: 20px;
  }

}

.card-service {
  position: relative;
  transition: all ease-in-out 1s;

  &:hover &__img {
    transform: scale(1.05);
  }

  &:hover &__link {
    color: $color-link;
  }

  &__img-wrap {
    width: 100%;
    height: 405px;
    overflow: hidden;
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    transform: scale(1);
    transition: all ease-in-out 1s;
  }

  &__icon-wrap {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: -55px auto 0;
    width: 110px;
    height: 110px;
    border-radius: 100%;
    background: #fff;
  }

  &__icon {
    width: 50px;
    height: 50px;

    & * {
      fill: $color-link;
    }

  }

  &__content-wrap {
    padding: 10px 20px 20px;
  }

  &__title {
    margin: 0;
    font-family: $font-base;
    font-weight: 500;
    font-size: 24px;
    color: $color-title;
    text-align: center;
    line-height: 1.3;
  }

  &__link {
    color: $color-title;
    text-decoration: none;
    transition: all ease-in-out .2s;

    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: "";
      z-index: 1;
    }

  }

  &__descr {
    margin: 10px 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    color: $color-description-light;
    text-align: center;
    line-height: 1.3;
  }

}

