.card-portfolio {
  width: 100%;
  height: 100%;
  min-height: 500px;
  position: relative;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .9;
  }

  &__img-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__content-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 100%;
    justify-content: flex-end;
  }

  &__content {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    padding: 40px 20px 20px;
    background: linear-gradient(-180deg, transparent, rgba(0, 0, 0, .05) 5%, rgba(0, 0, 0, .3) 25%, rgba(0, 0, 0, .6));
  }

  &__title {
    margin: 15px 0 0;
    font-family: $font-base;
    font-weight: normal;
    color: #fff;
    font-size: 28px;
    line-height: 1.3;
    text-shadow: 0 2px 2px rgba(0, 0, 0, .5);
  }

  &__link {
    color: #fff;
    text-decoration: none;

    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: "";
      z-index: 1;
    }

  }

}

.card-blog {
  width: 100%;
  height: 100%;
  min-height: 500px;
  position: relative;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .9;
  }

  &__img-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__content-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 100%;
    justify-content: flex-end;
  }

  &__content {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    padding: 60px 20px 20px;
    background: linear-gradient(-180deg, transparent, rgba(0, 0, 0, .05) 5%, rgba(0, 0, 0, .3) 25%, rgba(0, 0, 0, .6));
  }

  &__title {
    margin: 15px 0 0;
    font-family: $font-base;
    font-weight: 400;
    color: #fff;
    font-size: 28px;
    line-height: 1.3;
    text-shadow: 0 2px 2px rgba(0, 0, 0, .5);
  }

  &__link {
    color: #fff;
    text-decoration: none;

    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: "";
      z-index: 1;
    }

  }

  &__time {
    margin: 15px 0 0;
    font-family: $font-base;
    font-weight: 400;
    color: rgba(255, 255, 255, .5);
    font-size: 16px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .card-list {
    margin: -10px;

    $g: &;

    &_3 {

      #{$g}__item {
        width: 50%;
      }

    }

    &__item {
      width: 50%;
      padding: 10px;
    }

  }

  .card-service {

    &__img-wrap {
      height: 380px;
    }

  }

  .card-portfolio {
    min-height: 380px;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .card-list {
    margin: -10px;

    $g: &;

    &_3 {

      #{$g}__item {
        width: 100%;
      }

    }

    &__item {
      width: 100%;
      padding: 10px;
    }

  }

  .card-service {

    &__img-wrap {
      height: 300px;
    }

    &__icon-wrap {
      margin: -50px auto 0;
      width: 100px;
      height: 100px;
    }

    &__icon {
      width: 40px;
      height: 40px;
    }

    &__content-wrap {
      padding: 10px 0 15px;
    }

    &__title {
      font-size: 22px;
    }

    &__descr {
      font-size: 16px;
    }

  }

  .card-portfolio {
    min-height: 300px;

    &__title {
      font-size: 22px;
    }

  }

  .card-blog {
    min-height: 300px;

    &__content {
      padding: 40px 15px 15px;
    }

    &__title {
      font-size: 22px;
    }

    &__time {
      margin: 15px 0 0;
      font-size: 12px;
    }

  }

}

