.mask {
  display: none;
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .mask {
    display: block;
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateZ(0);
    transition: opacity .2s linear;
    visibility: hidden;
    z-index: 0;

    &.toggled {
      opacity: 1;
      visibility: visible;
      z-index: 4;
    }

  }

}

