.footer {
  padding: 70px 0 60px;
  background: #fcfaf7;

  &__logo-wrap {
    margin-top: 40px;
    display: flex;
    justify-content: center;
  }

}

.footer-nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style: none;
  margin: 40px 0 0;
  padding: 0;
  text-align: center;

  &__item:not(:first-child) {
    margin-left: 25px;
  }

  &__link {
    display: inline-block;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    text-decoration: none;
    transition: all ease-in-out .2s;

    &:hover {
      opacity: .9;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .footer {
    padding: 60px 0 50px;

    &__logo-wrap {
      margin-top: 20px;
    }

  }

  .footer-nav {
    margin: 20px 0 0;

    &__item:not(:first-child) {
      margin-left: 25px;
    }

    &__link {
      font-size: 18px;
    }

  }


}

