.contact {
  display: inline-flex;
  margin: 0;
  padding: 0;
  list-style: none;

  $g: &;

  &_footer {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    & #{$g}__item {
      margin: 15px 0 0!important;
      text-align: center;

      &:first-child {
        margin: 0!important;
      }

    }

    & #{$g}__link {

    }

    & #{$g}__img {

      & * {
        fill: $color-link;
      }

    }

    & #{$g}__text {
      color: $color-description;
    }

  }

  &__item:not(:first-child) {
    margin-left: 30px;
  }

  &__link {
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .7;
    }

  }

  &__img {
    margin-right: 10px;
    width: 20px;
    height: 20px;

    & * {
      fill: #fff;
    }

  }

  &__text {
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: #fff;
    line-height: 1;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .contact {

    $g: &;

    &_footer {
      margin-top: 20px;
    }

    &__item:not(:first-child) {
      margin-left: 30px;
    }

    &__img {
      margin-right: 10px;
      width: 18px;
      height: 18px;
    }

    &__text {
      font-size: 18px;
    }

  }


}

