.social {
  display: inline-flex;
  margin: 0;
  list-style: none;

  $g: &;

  &_footer {
    padding: 0;
    width: 100%;
    justify-content: center;

    #{$g}__img {
      fill: $color-link;;
      width: 36px;
      height: 36px;
    }

  }

  &__item:not(:first-child) {
    margin-left: 10px;
  }

  &__link {
    display: inline-block;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .7;
    }

  }

  &__img {
    vertical-align: top;
    fill: #ffffff;
    width: 22px;
    height: 22px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .social {

    $g: &;

    &_footer {

      #{$g}__img {
        width: 24px;
        height: 24px;
      }

    }

  }

}

